import React from 'react';

import { Button, notification, Popconfirm } from 'antd';
import { DeleteOutlined, SmileOutlined } from '@ant-design/icons';
import axios from 'axios';

export default class UserDeleteButton extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
        }
    }

    async delete_user() {
        this.setState({
            loading: true
        })
        for (let i = 0; i < this.props.selected.length; i++) {
            await axios.post('/api',
                {
                    api: 'user_delete',
                    user_id: this.props.selected[i],
                }).then(data => {
                    //console.log(data.data);
                    if (data.data.status === 0) {
                        notification.open({
                            message: '',
                            description:
                                `:${this.props.selected[i]} `,
                            icon: <SmileOutlined style={{ color: '#108ee9' }} />,
                        });
                    } else if (data.data.status === 1) {
                        notification.open({
                            message: '',
                            description:
                                ``,
                            icon: <SmileOutlined style={{ color: '#108ee9' }} />,
                        });
                    } else if (data.data.status === -1) {
                        notification.open({
                            message: '',
                            description:
                                ``,
                            icon: <SmileOutlined style={{ color: '#108ee9' }} />,
                        });
                    }
                });
        }
        this.setState({
            loading: false
        })
        this.props.onFresh();
    }

    render() {
        const { loading } = this.state
        return (
            <Popconfirm disabled={this.props.disabled} placement="top" title="" onConfirm={() => this.delete_user()} okText="" cancelText="">
                <Button
                    type="primary"
                    shape="round"
                    icon={<DeleteOutlined />}
                    danger
                    disabled={this.props.disabled}
                    loading={loading}
                >
                    
                </Button>
            </Popconfirm>
        );
    }
}