import React from 'react';
import { useParams } from 'react-router-dom'

import { Typography, Descriptions, Alert, PageHeader, Divider, message } from 'antd';
import axios from 'axios';
const { Paragraph } = Typography;

export default function ServerPsw(props) {
    let { server_id } = useParams();

    return (
        <div>
            <PageHeader
                ghost={false}
                title=""
                subTitle={`:${server_id}`}
            >
            </PageHeader>
            <Divider />
            <ServerPswShow server_id={server_id} />
        </div>
    );
}

class ServerPswShow extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: "",
            user: "",
            psw: ""
        }
    }

    componentDidMount() {
        this.fetch();
    }

    fetch() {
        axios.post('/api',
            {
                api: 'psw_check',
            }).then(data => {
                //console.log(data.data.id)
                this.setState({
                    id: data.data.id
                })
            });
        axios.post('/api',
            {
                api: 'server_ssh_info',
                server_id: this.props.server_id,
            }).then(data => {
                this.setState({
                    user: data.data.data.user,
                    psw: data.data.data.psw
                })
            });
    }

    onChangeUser = str => {
        const { user } = this.state;
        if (user === str) {
            return;
        }
        axios.post('/api',
            {
                api: 'server_change_user',
                server_id: this.props.server_id,
                server_user: str,
            }).then(data => {
                if (data.data.status === 0) {
                    this.setState({
                        user: str
                    })
                    message.success("")
                } else {
                    message.warning("")
                }
            }).catch(err => {
                message.error("")
            });
    };

    onChangePsw = str => {
        const { psw } = this.state;
        if (psw === str) {
            return;
        }
        axios.post('/api',
            {
                api: 'server_change_psw',
                server_id: this.props.server_id,
                server_psw: str,
            }).then(data => {
                if (data.data.status === 0) {
                    this.setState({
                        psw: str
                    })
                    message.success("")
                } else {
                    message.warning("")
                }
            }).catch(err => {
                message.error("")
            });
    };

    render() {
        const { id, user, psw } = this.state;
        return (
            <div>
                <Alert
                    message=""
                    description=""
                    type="info"
                    showIcon
                />
                <br />
                <Descriptions
                    title=""
                    bordered
                    column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
                >
                    <Descriptions.Item label="" span={4}>
                        <Paragraph copyable>{id}</Paragraph>
                    </Descriptions.Item>
                    <Descriptions.Item label="">
                        <Paragraph editable={{ onChange: this.onChangeUser }}>{user}</Paragraph>
                    </Descriptions.Item>
                    <Descriptions.Item label="">
                        <Paragraph editable={{ onChange: this.onChangePsw }}>{psw}</Paragraph>
                    </Descriptions.Item>
                </Descriptions>
            </div>
        );
    }
}
