import { DownOutlined, SmileOutlined } from '@ant-design/icons';
import { Button, Dropdown, Menu, message, notification } from 'antd';
import axios from 'axios';
import React from 'react';
import { Link } from 'react-router-dom';



export default class OneContainerActionButton extends React.Component {
    constructor(props) {
        super(props);

        this.menu = (
            <Menu onClick={(e) => this.handleMenuClick(e)}>
                <Menu.Item key="0">
                    <Link to={`${this.props.url}/create`}></Link>
                </Menu.Item>
                <Menu.Item key="1"></Menu.Item>
                <Menu.Item key="2"></Menu.Item>
                <Menu.Item key="3"></Menu.Item>
                <Menu.Item key="4"></Menu.Item>
            </Menu>
        );
    }

    async handleMenuClick(e) {
        this.props.onLoading();
        if (e.key === '4') {
            await axios.post('/api',
                {
                    api: 'container_delete_stoped',
                    server_id: this.props.server_id,
                }).then(data => {
                    //console.log(data.data);
                    notification.open({
                        message: '',
                        description:
                            ``,
                        icon: <SmileOutlined style={{ color: '#108ee9' }} />,
                    });
                });
            this.props.onFresh();
            return;
        } else if (e.key === '0') {
            this.props.onFresh();
            return;
        }
        if (this.props.selected.length === 0) {
            message.error('');
            this.props.onFresh();
            return;
        }
        if (e.key === '1') {
            for (let i = 0; i < this.props.selected.length; i++) {
                await axios.post('/api',
                    {
                        api: 'container_restart',
                        server_id: this.props.server_id,
                        container_id: this.props.selected[i],
                    }).then(data => {
                        //console.log(data.data);
                        notification.open({
                            message: '',
                            description:
                                `:${this.props.selected[i]}`,
                            icon: <SmileOutlined style={{ color: '#108ee9' }} />,
                        });
                    });
            }
        } else if (e.key === '2') {
            for (let i = 0; i < this.props.selected.length; i++) {
                await axios.post('/api',
                    {
                        api: 'container_stop',
                        server_id: this.props.server_id,
                        container_id: this.props.selected[i],
                    }).then(data => {
                        //console.log(data.data);
                        notification.open({
                            message: '',
                            description:
                                `:${this.props.selected[i]}`,
                            icon: <SmileOutlined style={{ color: '#108ee9' }} />,
                        });
                    });
            }
        } else if (e.key === '3') {
            for (let i = 0; i < this.props.selected.length; i++) {
                await axios.post('/api',
                    {
                        api: 'container_delete',
                        server_id: this.props.server_id,
                        container_id: this.props.selected[i],
                    }).then(data => {
                        //console.log(data.data);
                        notification.open({
                            message: '',
                            description:
                                `:${this.props.selected[i]}`,
                            icon: <SmileOutlined style={{ color: '#108ee9' }} />,
                        });
                    });
            }
        }
        this.props.onFresh();
    }

    render() {
        return (
            <div>
                <Dropdown overlay={this.menu}>
                    <Button disabled={this.props.disabled} loading={this.props.loading}>
                         <DownOutlined />
                    </Button>
                </Dropdown>
            </div>
        );
    }
}